import { useEffect } from "react";
import { Form, Button } from "react-bootstrap";
import * as Yup from "yup";
import { useFormik } from "formik";
import clsx from "clsx";
import axios from "axios";
import { useNavigate } from "react-router-dom";

import { API, routers, baseURL } from "API";
import { showToast } from "helpers";

function SetagainPassword(): JSX.Element {
  let navigate: any = useNavigate();

  useEffect((): void => {
    document.title = "t li mt khu";
  }, []);

  const formik: any = useFormik({
    initialValues: {
      password: "",
      confirm_password: "",
    },
    validationSchema: Yup.object({
      password: Yup.string()
        .required("Vui lng nhp mt khu")
        .min(8, "Mt khu ti thiu 8 k t")
        .matches(
          /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*]).{8,}$/,
          "Mt khu bao gm ch hoa, ch thng, s v k t c bit"
        ),
      confirm_password: Yup.string()
        .required("Vui lng nhp li mt khu")
        .oneOf([Yup.ref("password")], "Mt khu khng trng khp"),
    }),
    onSubmit: (value) => {
      axios.get(`${baseURL}/sanctum/csrf-cookie`).then(
        API.post(routers.user.setagainPassword, {
          email: localStorage.getItem("email_temporary"),
          passord: value.password,
        }).then((res: any): void => {
          localStorage.removeItem("email_temporary");
          showToast("success", res.data.message, 1500);
          setTimeout((): void => navigate("/signin"), 1000);
        })
      );
    },
  });

  return (
    <div className="px-4">
      <Form onSubmit={formik.handleSubmit}>
        <Form.Group className="mb-3" controlId="formBasicPassword">
          <Form.Label>Mt khu</Form.Label>
          <Form.Control
            type="password"
            placeholder="Nhp mt khu"
            name="password"
            value={formik.values.password}
            onChange={formik.handleChange}
          />
          {formik.errors.password && formik.touched.password ? (
            <Form.Text className="text-danger mt-5">
              {formik.errors.password}
            </Form.Text>
          ) : null}
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicConFirmPassword">
          <Form.Label>Nhp li mt khu</Form.Label>
          <Form.Control
            type="password"
            placeholder="Nhp li mt khu"
            name="confirm_password"
            value={formik.values.confirm_password}
            onChange={formik.handleChange}
          />
          {formik.errors.confirm_password && formik.touched.confirm_password ? (
            <Form.Text className="text-danger mt-5">
              {formik.errors.confirm_password}
            </Form.Text>
          ) : null}
        </Form.Group>

        <Form.Group className={clsx("w-100", "mb-4")}>
          <Button variant="danger" type="submit" className={clsx("w-100")}>
            Xc nhn
          </Button>
        </Form.Group>
      </Form>
    </div>
  );
}

export default SetagainPassword;
